  color: #665;
}

::selection {
  background-color: rgb(0 89 255);
  color: #fff;
}

input,
textarea {
  outline: none;
}

input:required:valid,
textarea:required:valid {
  border-color: green;
}

input:focus:required:invalid,
textarea:focus:required:invalid {
  border-color: red;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  line-height: 1.65;
  color: white;
  min-width: 320px;
  height: max(300px, 100vh);
  position: relative;
  overflow-x: hidden;
  background-color: rgb(2 9 41);
}

footer {
  width: 100%;
  min-height: 10vh;
  position: relative;
  text-align: center;
  color: darkgray;
  font-family: "Azeret Mono", monospace;
